
html,body{
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
.el-row:first-child {
  height: 85%;
  margin-bottom: 2%;
}
.chart{
  width: 100%;
  height: 100%;
}
.el-dialog{
  height: 70%;
}
.el-dialog__body{
    height: 70%;
    clear: all;
}
.full-height{
    height: 100%;
}



